import { useState } from "react";
import { motion } from "framer-motion";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';

const Menu = () => {
    const [isOpen, setIsOpen] = useState(false);

    const onClickShowMenu = (ev) => {
        const menuContainer = document.getElementById('menuContainer');
        setIsOpen(isOpen => !isOpen);
        menuContainer.classList.remove('hidden');
    }

    const onClickCloseMenu = (ev) => {
        const menuContainer = document.getElementById('menuContainer');
        setIsOpen(isOpen => !isOpen);
        menuContainer.classList.add('hidden');
    }
    const container = {
        hidden: { opacity: 0 },
        show: {
            opacity: 1,
            transition: {
                staggerChildren: 0.25
            }
        }
    }
    const item = {
        hidden: {
            opacity: 0,
            x: 0,
        },
        show: {
            opacity: 1,
            x: -50,
        }
    }

    return (
        <div className="absolute top-0 left-0 p-7" style={{ zIndex: '999' }}>
            <button aria-label="Ouvrir le menu" id="showMenu" onClick={onClickShowMenu} className="text-purple-950 focus:text-purple-600">
                <MenuIcon fontSize="large" />
            </button>
            <motion.div
                variants={container}
                id="menuContainer"
                initial="hidden"
                animate={isOpen ? "show" : "hidden"}
                className="absolute top-0 left-0 bg-gradient-to-br from-purple-950 to-purple-800 text-white hidden md:p-7"
                style={{ width: '100vw', height: '100vh' }}
                transition={{ duration: 0.25 }}
            >
                <button id="close" onClick={onClickCloseMenu} className="focus:hover:text-purple-200" aria-label="Fermer le menu">
                    <CloseIcon fontSize="large" />
                </button>
                <div className="flex flex-col justify-around items-end text-right h-3/4">
                    <motion.a variants={item} transition={{ duration: 0.5 }} className="w-11/12 md:w-1/2 lg:w-1/3 text-6xl border-b-4 border-white hover:text-purple-200 hover:border-purple-200 focus:text-purple-200 focus:border-purple-200 duration-150" href="/">Accueil</motion.a>
                    <motion.a variants={item} transition={{ duration: 0.5 }} className="w-11/12 md:w-1/2 lg:w-1/3 text-6xl border-b-4 border-white hover:text-purple-200 hover:border-purple-200 focus:text-purple-200 focus:border-purple-200 duration-150" href="/about">A propos</motion.a>
                    {/* <motion.a variants={item} transition={{ duration: 0.5 }} className="w-11/12 md:w-1/2 lg:w-1/3 text-6xl border-b-4 border-white hover:text-purple-200 hover:border-purple-200 focus:text-purple-200 focus:border-purple-200 duration-150" href="/projects">Projets</motion.a> */}
                    <motion.a variants={item} transition={{ duration: 0.5 }} className="w-11/12 md:w-1/2 lg:w-1/3 text-6xl border-b-4 border-white hover:text-purple-200 hover:border-purple-200 focus:text-purple-200 focus:border-purple-200 duration-150" href="/contact">Contact</motion.a>
                </div>
            </motion.div>
        </div>
    )
}

export default Menu;