import { useRef, useState } from "react";
import { motion } from 'framer-motion';
import CloseIcon from "@mui/icons-material/Close";
import PlaceIcon from '@mui/icons-material/Place';

const ExperienceCard = ({ experienceData }) => {
    const modalRef = useRef();
    const [isOpen, setIsOpen] = useState(false);
    // modale related stuff
    const showModal = (e) => {
        setIsOpen(isOpen => !isOpen);
        modalRef.current.className = "bg-gradient-to-br from-purple-950 to-purple-800 text-purple-100  modalDescription absolute top-0 left-0 ";
        modalRef.current.style.minHeight = document.body.scrollHeight + "px";
        window.scrollTo(0, 0)
    };
    const closeModal = (e) => {
        setIsOpen(isOpen => !isOpen);
        modalRef.current.className = 'hidden';
    };
    const container = {
        hidden: { opacity: 0 },
        show: {
            opacity: 1,
            transition: {
                staggerChildren: 0.25
            }
        }
    };
    const item = {
        hidden: { opacity: 0 },
        show: { opacity: 1 }
    }
    // Get date start and date end and convert to locate date (MMMM YYYY)
    const dateOptions = { month: "long", year: "numeric" };
    const dateStart = new Date(Date.parse(experienceData.dateStart));
    const dateEnd = new Date(Date.parse(experienceData.dateEnd));
    const dateStartString = dateStart.toLocaleDateString("fr-FR", dateOptions);
    const dateEndString = dateEnd.toLocaleDateString("fr-FR", dateOptions);

    return (
        <div className="flex flex-col justify-between p-2 w-full">
            <h4 className="text-xl border-b border-purple-950 mb-2">{experienceData.title}</h4>
            <span className="text-justify">{experienceData.shortDescription}</span>
            <button onClick={showModal} className="bg-purple-950 text-purple-100 rounded-sm px-1 focus:bg-purple-300 focus:text-purple-950 hover:bg-purple-300 hover:text-purple-950">En savoir plus</button>
            <motion.div
                variants={container}
                initial="hidden"
                animate={isOpen ? "show" : "hidden"} className="hidden" ref={modalRef} style={{ zIndex: 1000 }}>
                <button onClick={closeModal} className="focus:hover:text-purple-200 absolute top-3 right-3" aria-label="Fermer la modale">
                    <CloseIcon fontSize="large" />
                </button>
                <div className="page flex justify-center items-center">
                    <div className="cardDetails p-4 lg:w-3/4 w-full">
                        <motion.div className="titleMotion" variants={item} transition={{ duration: .5 }}>
                            <div className="flex justify-between items-center">
                                <h3 className="text-3xl">{experienceData.title}</h3>
                                <span className="text-sm me-10 lg:me-0">{dateStartString} - {dateEndString}</span>
                            </div>
                            <svg aria-hidden="true" preserveAspectRatio="xMaxYMid meet" viewBox="0 0 529 2">
                                <line x1="0" x2="529" style={{ stroke: '#f3e8ff', strokeWidth: '2px' }} />
                            </svg>
                            <span className="italic text-sm">{experienceData.shortDescription}</span>
                            <p><PlaceIcon /> {experienceData.location.name}</p>
                        </motion.div>


                        <motion.div className="descriptionMotion" variants={item} transition={{ duration: .5 }}>

                            <h4 className="text-2xl mt-2">Description</h4>
                            <p className="text-justify">{experienceData.placeDescription}</p>
                            <p className="text-justify">{experienceData.activitiesDescription}</p>
                        </motion.div>
                        <motion.div className="technologiesMotion" variants={item} transition={{ duration: .5 }}>
                            <h4 className="text-2xl mt-2">Technologies</h4>
                            <div className="grid lg:grid-cols-3 md:grid-col-2 grid-cols-1 gap-4">
                                <div className="bg-purple-100 text-purple-950 p-2 rounded-sm shadow-sm">
                                    <h5 className="text-xl">Langages :</h5>
                                    {experienceData.technologies.languages.map((item) => <p>{item}</p>)}
                                </div>
                                <div className="bg-purple-100 text-purple-950 p-2 rounded-sm shadow-sm">
                                    <h5 className="text-xl">Frameworks :</h5>
                                    {experienceData.technologies.frameworks.map((item) => <p>{item}</p>)}
                                </div>
                                <div className="bg-purple-100 text-purple-950 p-2 rounded-sm shadow-sm">
                                    <h5 className="text-xl">Autres :</h5>
                                    {experienceData.technologies.others.map((item) => <p>{item}</p>)}
                                </div>
                            </div>
                        </motion.div>

                    </div>
                </div>
            </motion.div>
        </div>
    );
}

export default ExperienceCard;