import { motion } from "framer-motion";

const lineDrawing = {
    hidden: { pathLength: 0, opacity: 0 },
    visible: (i) => {
        const delay = i;
        return {
            pathLength: 1,
            opacity: 1,
            transition: {
                pathLength: { delay, type: "spring", duration: .75, bounce: 0 },
                opacity: { delay, duration: 0.01 }
            }
        };
    }
};

const SubtitleWithStroke = ({title, custom}) => {
    return (
        <div className="title lg:w-1/2 mx-auto">
            <motion.h3 className="text-3xl"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 0.5 }}>
                {title}
            </motion.h3>
            <motion.svg aria-hidden="true" preserveAspectRatio="xMaxYMid meet" viewBox="0 0 529 2" initial="hidden" animate="visible">
                <motion.line x1="0" x2="529" style={{ stroke: '#3b0764', strokeWidth: '3px' }} variants={lineDrawing} custom={custom} />
            </motion.svg>
        </div>
    );
};

export default SubtitleWithStroke;