import useMousePosition from "../../hooks/Mouse";
import { motion } from "framer-motion";

const BlurredMouse = ({yOffset, xOffset}) => {
    const mousePosition = useMousePosition();
    if (mousePosition)
    return (
        <motion.div 
            className="BlurredMouse hidden xl:block relative blurred-mouse-box blur-3xl rounded-full w-80 h-80 z-0 bg-violet-200" 
            animate={{
                y: `calc(${mousePosition.y}px - ${yOffset})`, 
                x: `calc(${mousePosition.x}px - ${xOffset})`
            }}
            transition={{
                type: "tween",
            }}
        />
    )
}
export default BlurredMouse;