import { useEffect, useRef, useState } from "react";
import emailjs from '@emailjs/browser';
import HCaptcha from "@hcaptcha/react-hcaptcha";
import Menu from "../Menu/Menu";



const ContactForm = () => {
    const form = useRef();
    const captchaRef = useRef();
    const [token, setToken] = useState(null);


    const onLoadCaptcha = () => {
        captchaRef.current.execute();
    }

    useEffect(() => {
        if (token) {
            // Do something
        }
    }, [token]);

    /**
     * 
     * @param {SubmitEvent} e 
     */
    const sendEmail = (e) => {
        emailjs.sendForm(process.env.REACT_APP_EMAILJS_SERVICE_ID, process.env.REACT_APP_EMAILJS_TEMPLATE_ID, form.current, process.env.REACT_APP_EMAILJS_PUBLIC_KEY)
            .then((result) => { window.location.href = '/success-mail' }, (error) => { });
    };
    return (
        <div className="ContactForm border border-white md:w-1/3 sm:w-3/4 w-full p-3 text-purple-950">
            <Menu />
            <form ref={form} className="contactFormBody" autoComplete="on" name="contactForm" onSubmit={sendEmail}>
                <label htmlFor="reply_to" className="text-xs italic">E-mail</label>
                <input id="reply_to" className="placeholder:italic placeholder:text-slate-400 block bg-white w-full border-b border-slate-600 py-2 px-3 shadow-sm focus:outline-none focus:border-purple-500 sm:text-sm mb-2"
                    placeholder="E-mail" type="email" name="reply_to" required />
                <label htmlFor="from_name" className="text-xs italic">Nom et prnom</label>
                <input className="placeholder:italic placeholder:text-slate-400 block bg-white w-full border-b border-slate-600 py-2 px-3 shadow-sm focus:outline-none focus:border-purple-500 sm:text-sm mb-2"
                    placeholder="Votre nom" type="text" name="from_name" required />
                <label className="text-xs italic" htmlFor="subject">Sujet</label>
                <select className="placeholder:italic placeholder:text-slate-400 block bg-white w-full border-b border-slate-600 py-2 px-3 shadow-sm focus:outline-none focus:border-purple-500 sm:text-sm mb-2"
                    name="subject" required >
                    <option>Contact pour emploi</option>
                    <option>Contact pour mission</option>
                    <option>Autre</option>
                </select>
                <label htmlFor="message" className="text-xs italic">Contenu</label>
                <textarea className="placeholder:italic placeholder:text-slate-400 block bg-white w-full border border-slate-600 py-2 px-3 shadow-sm focus:outline-none focus:border-purple-500 focus:ring-purple-500 focus:ring-1 sm:text-sm mb-2"
                    placeholder="Prenez votre inspiration, et laissez vous porter par votre crativit" name="message" rows="5" required />
                <div className="flex flex-col justify-center items-center">
                    <HCaptcha sitekey={process.env.REACT_APP_CAPTCHA_SITEKEY} onVerify={setToken} onLoad={onLoadCaptcha} ref={captchaRef} />
                    <button type="submit" className="mt-3 px-4 text-2xl text-white bg-purple-950 focus:bg-purple-100 focus:text-purple-950 hover:bg-purple-100 hover:text-purple-950">Envoyer</button>
                </div>
            </form>
        </div>
    )
}

export default ContactForm;